.tours-preview-wrapper {
	width: 104%;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	margin-top: -3vw; }

.tour-preview {
	width: 33%;
	padding-right: 45px;
	margin-top: 3vw;
	color: inherit;
	overflow: hidden;
	&:hover {
		.tour-preview-h3 {
			color: $accent-color;
			&::after {
				transform: translateX(0%);
				background: $accent-color; } }
		.tour-img-wrapper {
			&::after {
				opacity: 1; } } } }


.tour-img-wrapper {
	width: 100%;
	height: 16vw;
	overflow: hidden;
	background-size: cover;
	background-position: 50% 50%;
	position: relative;
	// background-color: gray
	transition: transform 0.2s ease;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(black, 0.3);
		opacity: 0;
		transition: opacity 0.3s ease; } }

.tour-preview-h3 {
	font-size: 20px;
	margin-top: 16px;
	display: flex;
	flex-direction: column;
	width: fit-content;
	overflow: hidden;
	transition: color 0.3s ease;
	&::after {
		content: '';
		width: 50%;
		height: 2px;
		background: $dark-color;
		display: block;
		transform: translateX(-100%);
		transition: transform 0.3s ease, background 0.3s ease; } }
