@media (max-width: 850px) {

	.tab-2, .tab-3 {
		h2 {
			width: 100%; } }
	.choose-items__item {
		flex-basis: 100%;
		margin-right: 0px; }
	.bottom-content {
		&.welcome {
			img {
				max-width: 100%;
				&:nth-child(2) {
					display: none; } } } }
	.top-content {
		width: 100%; }

	.main-footer {
		&__content {
			flex-direction: column;
			&__left-content, &__right-content {
				width: 100%; }
			&__right-content {
				margin-top: 30px; } } }
	.bottom-content {
		margin-top: 40px; }
	.img-outer {
		height: 221px;
		width: 284px; }
	.strip-nav__line {
		width: 100%;
		height: 1px;
		background: rgba(#292B33, 0.2); }
	.input-wrapper {
		margin-top: 40px; }
	.waves-wrapper {
		bottom: -35vh; }
	.footer__container {
		padding-bottom: 60px; }
	.strip-nav {
		width: 100%;
		padding-right: calc(5vw + 20px);
		&__left {
			margin-right: 20px; }
		&__right {
			margin-left: 20px; } }
	.nav__items {
		pointer-events: none;
		position: fixed;
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;
		z-index: 100;
		opacity: 0;
		transform: translateX(100%);
		transition: transform .5s cubic-bezier(0.445, 0.05, 0.55, 0.95), opacity .5s cubic-bezier(0.445, 0.05, 0.55, 0.95);
		&__wrapper {
			height: auto;
			min-height: 100vh;
			width: 100%;
			flex-direction: column;
			justify-content: center;
			background: white;
			padding-top: 20px;
			padding-bottom: 20px;
			padding-left: 20px; }
		&__wrap--wrapper {
			width: 100%;
			overflow-y: scroll;
			overflow-x: hidden; }

		&.show {
			pointer-events: auto;
			opacity: 1;
			transform: translateX(0px); } }
	.nav {
		position: fixed;
		top: 3%;
		&__logo {
			z-index: 10011;
			&.invisible {
				opacity: 0;
				pointer-events: 0; } } }

	.nav__item {
		margin-left: 0px;
		transform: translateX(100%);
		transition: transform .5s cubic-bezier(0.645, 0.045, 0.355, 1);
		padding-bottom: 20px;
		width: fit-content;
		&.active {
			transform: translateX(0%);
			@for $i from 1 to 5 {
				&:nth-child(#{$i}) {
					transition-delay: $i * 0.08s; } } } }
	.strip-link {
		font-size: 18px; }
	.burger {
		display: block;
		background: white;
		border-radius: 50%;
		width: 40px;
		height: 40px; }
	.strip-after {
		width: 95%; }
	.draggable {
		display: none; }
	.strip {
		pointer-events: auto; }
	.strip-outer {
		overflow-x: scroll; }
	.strip-nav {
		display: none; }
	.dropbtn {
		pointer-events: none;
		color: $accent-color; }
	.dropdown-content {
		background: transparent;
		margin-top: 0px;
		position: relative;
		display: flex;
		opacity: 1;
		pointer-events: auto;
		justify-content: center;
		flex-direction: column;
		padding-top: 30px;
		height: auto;
		padding-left: 10px;
		box-shadow: none;
		a {
			padding: 0px 0px;
			padding-bottom: 20px; } }
	.main-footer__content__left-content__contacts-items.float-right {
		align-items: flex-start; }
	.social-media {
		a {
			margin-left: 0px;
			margin-right: 20px; } }
	.white-block {
		flex-direction: column;
		padding: 18px 70px;
		&__item {
			margin-left: 0px;
			margin-top: 24px;
			line-height: 1em;
			&.first {
				margin-top: 0px; } } }
	.tour-header__tour-content {
		width: 100%; }
	.tour-h1 {
		font-size: 36px !important; }
	.price__item {
		flex-basis: 100%; }
	.page-contacts {
		height: auto;
		min-height: 100vh;
		&__wrapper {
			flex-direction: column;
			padding-right: 0px; } }
	.page-contacts-wrapper {
		&__left {
			width: 100%;
			height: 30vh; }
		&__right {
			width: 100%;
			padding-left: 20px;
			padding-top: 60px;
			padding-bottom: 100px;
			.button {
				margin-top: 40px; }
			.main-footer__content__left-content__contacts-items {
				&.with-margin {
					margin-top: 40px; } } } }
	.history-wrapper {
		flex-direction: column;
		&__left {
			width: 100%;
			height: 50vh; }
		&__right {
			width: 100%;
			margin-left: 0px;
			margin-top: 30px; } }
	.reviews-content-wrapper {
		flex-direction: column-reverse;
		&__left {
			width: 100%;
			margin-top: 30px;
			font-size: 18px;
			margin-bottom: 50px;
			.TA_cdsratingsonlynarrow {
				margin-top: 86px; }
			.sec-p {
				margin-top: 36px; } }
		&__right {
			width: 100%;
			margin-top: 36px; } }
	.reviews-item {
		padding-bottom: 33px; }
	.only-mobile {
		display: block; }
	.review-header-desc {
		font-size: 18px;
		margin-top: 15px; } }

